<template>
    <div style="width:90%">
        <div class="mb-6">
            <h2>Informacion del cliente</h2>
        </div>
        <div>
            <h3 class="mb-3">
                Nombre: {{ cliente.nombre }}
            </h3>
            <h3 class="mb-3">
                Apellido: {{ cliente.apellido }}
            </h3>
            <h3 class="mb-3">
                Correo: {{ cliente.correo }}
            </h3>
            <h3 class="mb-3">
                Dirección: {{ cliente.direccion }}
            </h3>
            <h3 class="mb-3">
                Telefono: {{ cliente.telefono }}
            </h3>
            <div class="d-flex justify-end mt-6">
                <v-btn @click="accionModificarCliente">
                    <v-icon>mdi-pencil</v-icon>
                    Modificar
                </v-btn>
            </div>
        </div>
        <div class="mt-6 mb-6">
            <h2>Rutina asignada al cliente</h2>
        </div>
        <div>
            <v-form>
                <v-row 
                v-if="cliente.idSemana"
                no-gutters
                class="mb-2">
                    <v-btn style="width:82%" @click="push(cliente.idSemana)">
                        {{ nombrePlan(cliente.idSemana) }}
                    </v-btn>
                    <v-btn style="width:4%" color="red" @click="eliminarPlanAsignado(cliente.id)">
                    <v-icon>mdi-delete</v-icon>
                    </v-btn>
                </v-row>
            </v-form>
            <div class="d-flex justify-end mt-6">
                    <v-btn @click="this.$router.back()">
                        <v-icon>mdi-reply</v-icon>
                        <span>Volver</span>
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-btn :disabled="condicion" @click="dialogPlan=true">
                        <v-icon>mdi-plus</v-icon>
                        <span>Añadir Nuevo</span>
                    </v-btn>
                </div>
        </div>
        <v-dialog 
        v-model="dialogCliente">
            <v-card class="pa-4">
                <v-form class="pa-4">
                    <v-row>
                        <v-text-field                                
                        label="Nombre"
                        v-model="infoTemplate.nombre"
                        >
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field                                
                        label="Apellido"
                        v-model="infoTemplate.apellido"
                        >
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field                                
                        label="Correo"
                        v-model="infoTemplate.correo"
                        >
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field                                
                        label="Dirección"
                        v-model="infoTemplate.direccion"
                        >
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field                                
                        label="Teléfono"
                        v-model="infoTemplate.telefono"
                        >
                        </v-text-field>
                    </v-row>
                </v-form>
                <div>
                    <v-row class="pa-4">
                        <v-btn @click="cerrarDialogCliente" >
                            <v-icon>mdi-close-box</v-icon>
                            Cerrar
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn @click="accionGuardarCliente" >
                            <v-icon>mdi-content-save-edit</v-icon>
                            Guardar
                        </v-btn>
                    </v-row>
                </div>
            </v-card>
        </v-dialog>
        <v-dialog
        v-if="dialogPlan"
        v-model="dialogPlan">
            <v-card class="pa-3">
                <v-card-title class="mb-2">Seleccione Rutina</v-card-title>
                <v-row
                class="pa-2"
                v-for="(plan, index) in listadoPlanes"
                :key="plan.id">
                    <v-btn style="width:81%">
                        {{ plan.nombrePlanSemanal }}
                    </v-btn>
                    <v-btn style="width:4%" color="green" @click="asignarPlan(plan.idPlanSemanal)">
                        <v-icon>mdi-check</v-icon>
                    </v-btn>
                </v-row>
                <div class="d-flex justify-start mt-6">
                    <v-btn @click="dialogPlan=false" >
                        <v-icon>mdi-close-box</v-icon>
                        Cerrar
                    </v-btn>
                </div>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import utiles from '../utiles/funciones'
export default{
    data () {
            return{
                cliente: this.$store.getters.clienteEspecifico(this.$route.params.id),
                infoTemplate: {},
                dialogCliente: false,
                dialogPlan: false
            }
        },
    methods:{
        accionGuardarCliente(){
            this.$store.commit('guardarInfoCliente', this.infoTemplate)
            this.cerrarDialogCliente()
        },
        accionModificarCliente(){
            this.infoTemplate = this.cliente,
            this.dialogCliente = true
        },
        cerrarDialogCliente(){
            this.dialogCliente = false,
            this.infoTemplate ={
                id: 0,
                nombre: "",
                apellido: "",
                correo: "",
                direccion: "",
                telefono: "",
                idSemana: 0
            }
        },

        asignarPlan(idPlan){
            this.$store.commit('asignarPlanSemanal', {
                idPlan:idPlan,
                idCliente:this.cliente.id})
            this.dialogPlan=false
        },
        eliminarPlanAsignado(id){
            this.$store.commit('eliminarPlanAsignado', id)
        },
        push(idSemana) {
            return this.$router.push(`/entrenador/entrenamientos/${idSemana}`)
        },
        nombrePlan(id){
            return utiles.encontrarNombrePlan(id)
        }
    },
    computed: {
        condicion(){
            return (this.cliente.idSemana!=0 ? true : false)
        },
        listadoPlanes(){
            return this.$store.getters.listadoPlanesSemanales
        }
    }

}

</script>